.footer {
  height: 100%;
  display: flex;
  margin-top: 5%;
  margin-left: 50px;
}

.fotterimage {
  padding-left: 2%;
  padding-bottom: 2%;
  height: 33px;
}

.fotterimage fb {
  width: 23.96px;
  margin-bottom: 10px;
}

.fotterimage insta {
  width: 35.9px;
}

.fotterimage twitter {
  width: 41px;
  margin-bottom: 2%;
}

.fotterimage youtube {
  width: 38.87px;
}

.fotterimage in {
  width: 35px;
}

.footertext {
  color: white;
  align-items: center;
  display: flex;
  padding-bottom: 2%;
  text-align: end;
  padding-left: 50%;
}

.footerbutton {
  background: none;
  border: none;
  margin-bottom: 20px;
  margin-left: 1%;
  cursor: pointer;
}

.visibility{
  display: none;
}


@media only screen and (max-width: 900px) {
  .footer {
    display: inline-block;
    margin-left: 20px;
    margin-top: 10%;
  }

  .footer .footertext {
    zoom: 80%;
    text-align: start;
    padding-left: 40px;
    padding-right: 50px;
    font-size: 12px;
  }
  .footer .fotterimage .fb {
    display: none;
    width: 9.96px;
    margin-bottom: 10px;
  }
  .footer .fotterimage insta {
    display: block;
    width: 9.96px;
    margin-bottom: 10px;
  }

  .footer .fotterimage {
    margin-left: 12px;
  }
}

@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) and (-webkit-min-device-pixel-ratio: 1) {
  .footer {
    zoom: 80%;
    margin-top: 10%;
  }
  .footer .footertext {
    padding-left: 40%;
  }
}

@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) and (-webkit-min-device-pixel-ratio: 2) and (min-resolution: 192dpi) {
  .footer {
    zoom: normal;
  }
  .footer .footertext {
    padding-left: 35%;
  }
}


@media only screen and (min-width: 600px) and (max-device-width :900px) {
  .footer {
    zoom: 79%;
    margin-top: -70%;
    margin-bottom: -90%;
  }

}